import React, {useState} from "react";
import styles from "./Post.module.css"
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import { faHeart } from '@fortawesome/free-solid-svg-icons'


type MessageType = {
    id: string
    message: string
    likes?: number
    addLike: (id: string) => void
    decreaseLike: (id: string) => void
}
const Post = (props: MessageType) => {

    let [likeCount, setCount] = useState<'0' | '1'>('0')
    let likeChanger = () => {
        if (likeCount === '0') {
            props.addLike(props.id)
            setCount('1')
        } else {
            props.decreaseLike(props.id)
            setCount('0')
        }
    }


    return (
        <div>
            <div className={styles.item}>
                <img className={styles.img__ava}
                     src="data:image/jpeg;base64,"
                     alt=""/>
                {props.message}
                <div className={styles.likes__container}>
                    < FontAwesomeIcon
                        onClick={likeChanger}
                        className={likeCount === '0' ? styles.heart : `${styles.heart} ${styles.heart_liked}`}
                        icon={ faHeart }/>
                    {props.likes}
                </div>
            </div>


        </div>
    )
}
export default Post;

